"use client";

import { useCallback, useRef } from "react";
import { usePathname, useRouter } from "next/navigation";
import { useAuth } from "@/lib/supabase/auth-provider";
import { useLoginRequired } from "@/components/ui/Dialog";
import { saveRedirectUrl, securityUtils } from "@/utils/auth-redirect";

export interface AuthGuardOptions {
    redirectUrl?: string;
    requireAuth?: boolean;
    onAuthRequired?: (redirectUrl?: string) => void;
    onAuthSuccess?: () => void;
    onError?: (error: Error) => void;
    customLoginMessage?: {
        title?: string;
        description?: string;
        loginText?: string;
        cancelText?: string;
    };
}

export function useAuthGuard(options: AuthGuardOptions = {}) {
    const { isAuthenticated, isLoading, user } = useAuth();
    const router = useRouter();
    const pathname = usePathname();
    const showLoginRequired = useLoginRequired();
    const hasLoggedInitRef = useRef(false);

    // useLoginRequired ÌõÖÏù¥ Ï†úÎåÄÎ°ú Í∞ÄÏ†∏ÏôÄÏ°åÎäîÏßÄ ÌôïÏù∏ (Ìïú Î≤àÎßå)
    if (!hasLoggedInitRef.current && process.env.NODE_ENV === 'development') {
        console.log('üîç [useAuthGuard] Ï¥àÍ∏∞Ìôî:', {
            hasShowLoginRequired: typeof showLoginRequired === 'function',
            pathname,
            timestamp: new Date().toISOString()
        });
        hasLoggedInitRef.current = true;
    }

    const {
        redirectUrl,
        requireAuth = true,
        onAuthRequired,
        onAuthSuccess,
        onError,
        customLoginMessage,
    } = options;

    // Ïù∏Ï¶ù Ï≤¥ÌÅ¨ Ìï®Ïàò
    const checkAuth = useCallback(async (): Promise<boolean> => {
        try {
            console.log("üîç checkAuth ÏãúÏûë:", {
                isLoading,
                requireAuth,
                isAuthenticated,
                userId: user?.id,
                timestamp: new Date().toISOString(),
            });

            // Î°úÎî© Ï§ëÏù¥Î©¥ ÎåÄÍ∏∞
            if (isLoading) {
                console.log("‚è≥ Î°úÎî© Ï§ë - Ïù∏Ï¶ù Ï≤¥ÌÅ¨ ÎåÄÍ∏∞");
                return false;
            }

            // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
            if (!requireAuth) {
                console.log("‚úÖ Ïù∏Ï¶ù Î∂àÌïÑÏöî - ÌÜµÍ≥º");
                return true;
            }

            // Î≥¥Ïïà Í≤ÄÏ¶ù
            if (!securityUtils.validateUserAgent()) {
                console.log("üö´ Î≥¥Ïïà Í≤ÄÏ¶ù Ïã§Ìå® - ÏùòÏã¨Ïä§Îü¨Ïö¥ ÏÇ¨Ïö©Ïûê ÏóêÏù¥Ï†ÑÌä∏");
                const error = new Error(
                    "Î≥¥Ïïà Í≤ÄÏ¶ù Ïã§Ìå®: ÏùòÏã¨Ïä§Îü¨Ïö¥ ÏÇ¨Ïö©Ïûê ÏóêÏù¥Ï†ÑÌä∏",
                );
                onError?.(error);
                return false;
            }

            // Ïù∏Ï¶ù ÏÉÅÌÉú ÏóÑÍ≤© Ï≤¥ÌÅ¨
            const hasValidAuth = isAuthenticated && user && user.id;

            console.log("üîê Ïù∏Ï¶ù ÏÉÅÌÉú Ï≤¥ÌÅ¨:", {
                isAuthenticated,
                hasUser: !!user,
                userId: user?.id,
                hasValidAuth,
            });

            // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏùò ÏûòÎ™ªÎêú Ïù∏Ï¶ù Îç∞Ïù¥ÌÑ∞ Ï†ïÎ¶¨
            if (!hasValidAuth) {
                console.log("üßπ ÏûòÎ™ªÎêú Ïù∏Ï¶ù Îç∞Ïù¥ÌÑ∞ Ï†ïÎ¶¨ ÏãúÏûë");
                try {
                    // Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏïòÎäîÎç∞ ÎÇ®ÏïÑÏûàÎäî Ïù∏Ï¶ù Í¥ÄÎ†® Îç∞Ïù¥ÌÑ∞ Ï†ïÎ¶¨
                    const authKeys = [
                        "auth_success",
                        "auth_provider",
                        "auth_timestamp",
                    ];
                    authKeys.forEach((key) => {
                        if (localStorage.getItem(key)) {
                            localStorage.removeItem(key);
                            console.log(`üóëÔ∏è ÏûòÎ™ªÎêú Ïù∏Ï¶ù Îç∞Ïù¥ÌÑ∞ Ï†ïÎ¶¨: ${key}`);
                        }
                    });
                } catch (e) {
                    console.warn("Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄ Ï†ïÎ¶¨ Ï§ë Ïò§Î•ò:", e);
                }
            }

            // Ïù∏Ï¶ùÎêú Í≤ΩÏö∞
            if (hasValidAuth) {
                console.log("‚úÖ Ïù∏Ï¶ù ÏÑ±Í≥µ");
                onAuthSuccess?.();
                return true;
            }

            // Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
            console.log("‚ùå Ïù∏Ï¶ù Ïã§Ìå® - Î°úÍ∑∏Ïù∏ ÌïÑÏöî");
            const targetUrl = redirectUrl || pathname;

            // URL Î≥¥Ïïà Í≤ÄÏ¶ù
            if (targetUrl && !securityUtils.isValidRedirectUrl(targetUrl)) {
                console.warn("Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Î¶¨Îã§Ïù¥Î†âÌä∏ URL:", targetUrl);
                onAuthRequired?.();
                return false;
            }

            onAuthRequired?.(targetUrl);
            return false;
        } catch (error) {
            console.error("Ïù∏Ï¶ù Ï≤¥ÌÅ¨ Ï§ë Ïò§Î•ò:", error);
            onError?.(
                error instanceof Error ? error : new Error("Ïù∏Ï¶ù Ï≤¥ÌÅ¨ Ïã§Ìå®"),
            );
            return false;
        }
    }, [
        isLoading,
        requireAuth,
        isAuthenticated,
        user,
        redirectUrl,
        pathname,
        onAuthRequired,
        onAuthSuccess,
        onError,
    ]);

    // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú Ïï°ÏÖòÏùÑ Ïã§ÌñâÌïòÎäî Ìï®Ïàò
    const withAuth = useCallback(async <T>(
        action: () => Promise<T> | T,
        customRedirectUrl?: string,
    ): Promise<T | null> => {
        try {
            console.log("üîê withAuth ÏãúÏûë:", {
                isLoading,
                isAuthenticated,
                userId: user?.id,
                requireAuth,
                timestamp: new Date().toISOString(),
            });

            const isAuthorized = await checkAuth();

            console.log("üîç checkAuth Í≤∞Í≥º:", isAuthorized);

            if (!isAuthorized) {
                console.log("‚ùå Ïù∏Ï¶ù Ïã§Ìå® - Î°úÍ∑∏Ïù∏ Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÌëúÏãú");
                const targetUrl = customRedirectUrl || redirectUrl || pathname;

                // URL Î≥¥Ïïà Í≤ÄÏ¶ù Î∞è Ï†ÄÏû•
                if (targetUrl && securityUtils.isValidRedirectUrl(targetUrl)) {
                    console.log("Î¶¨Îã§Ïù¥Î†âÌä∏ URL Ï†ÄÏû•:", targetUrl);
                    saveRedirectUrl(targetUrl);
                }

                // Î°úÍ∑∏Ïù∏ Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÌëúÏãú
                console.log("üîÑ showLoginRequired Ìò∏Ï∂ú ÏãúÏûë:", {
                    targetUrl,
                    hasCustomMessage: !!customLoginMessage,
                    timestamp: new Date().toISOString()
                });
                
                const loginDialogResult = await showLoginRequired({
                    redirectUrl: targetUrl,
                    title: customLoginMessage?.title,
                    description: customLoginMessage?.description,
                    loginText: customLoginMessage?.loginText,
                    cancelText: customLoginMessage?.cancelText,
                    onLogin: () => {
                        // URLÏùÄ Ïù¥ÎØ∏ Ï†ÄÏû•ÎêòÏóàÏúºÎØÄÎ°ú Ï§ëÎ≥µ Ï†ÄÏû•ÌïòÏßÄ ÏïäÏùå
                        console.log("üîÑ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô");
                        router.push("/login");
                    },
                });

                console.log("üîÑ showLoginRequired Í≤∞Í≥º:", loginDialogResult);

                return null;
            }

            console.log("‚úÖ Ïù∏Ï¶ù ÏÑ±Í≥µ - Ïï°ÏÖò Ïã§Ìñâ");
            // Ïù∏Ï¶ùÎêú Í≤ΩÏö∞ Ïï°ÏÖò Ïã§Ìñâ
            return await action();
        } catch (error) {
            console.error("Ïù∏Ï¶ù Ïï°ÏÖò Ïã§Ìñâ Ï§ë Ïò§Î•ò:", error);
            onError?.(
                error instanceof Error ? error : new Error("Ïï°ÏÖò Ïã§Ìñâ Ïã§Ìå®"),
            );
            return null;
        }
    }, [
        checkAuth,
        redirectUrl,
        pathname,
        showLoginRequired,
        router,
        onError,
        customLoginMessage,
        isLoading,
        isAuthenticated,
        user,
        requireAuth,
    ]);

    // Ïù∏Ï¶ùÏù¥ ÌïÑÏöîÌïú ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò
    const navigateWithAuth = useCallback(async (
        path: string,
        options?: { replace?: boolean },
    ): Promise<boolean> => {
        try {
            // Í≤ΩÎ°ú Î≥¥Ïïà Í≤ÄÏ¶ù
            if (!securityUtils.isValidRedirectUrl(path)) {
                console.warn("Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Í≤ΩÎ°ú:", path);
                onError?.(new Error("Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Í≤ΩÎ°ú"));
                return false;
            }

            const isAuthorized = await checkAuth();

            if (!isAuthorized) {
                console.log("ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î¶¨Îã§Ïù¥Î†âÌä∏ URL Ï†ÄÏû•:", path);
                saveRedirectUrl(path);

                // Î°úÍ∑∏Ïù∏ Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÌëúÏãú
                console.log("üîÑ showLoginRequired Ìò∏Ï∂ú ÏãúÏûë:", {
                    path,
                    hasCustomMessage: !!customLoginMessage,
                    timestamp: new Date().toISOString()
                });
                
                const loginDialogResult = await showLoginRequired({
                    redirectUrl: path,
                    title: customLoginMessage?.title,
                    description: customLoginMessage?.description,
                    loginText: customLoginMessage?.loginText,
                    cancelText: customLoginMessage?.cancelText,
                    onLogin: () => {
                        // URLÏùÄ Ïù¥ÎØ∏ Ï†ÄÏû•ÎêòÏóàÏúºÎØÄÎ°ú Ï§ëÎ≥µ Ï†ÄÏû•ÌïòÏßÄ ÏïäÏùå
                        console.log("üîÑ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô");
                        router.push("/login");
                    },
                });

                console.log("üîÑ showLoginRequired Í≤∞Í≥º:", loginDialogResult);

                return false;
            }

            // Ïù∏Ï¶ùÎêú Í≤ΩÏö∞ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ïã§Ìñâ
            if (options?.replace) {
                router.replace(path);
            } else {
                router.push(path);
            }

            return true;
        } catch (error) {
            console.error("Ïù∏Ï¶ù ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ï§ë Ïò§Î•ò:", error);
            onError?.(
                error instanceof Error ? error : new Error("ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ïã§Ìå®"),
            );
            return false;
        }
    }, [checkAuth, showLoginRequired, router, onError, customLoginMessage]);

    return {
        // ÏÉÅÌÉú
        isAuthenticated,
        isLoading,
        user,

        // Î©îÏÑúÎìú
        checkAuth,
        withAuth,
        navigateWithAuth,
    };
}

// Ìé∏Ïùò ÌõÖÎì§
export function useRequireAuth(
    options?: Omit<AuthGuardOptions, "requireAuth">,
) {
    return useAuthGuard({ ...options, requireAuth: true });
}

export function useOptionalAuth(
    options?: Omit<AuthGuardOptions, "requireAuth">,
) {
    return useAuthGuard({ ...options, requireAuth: false });
}
